<template>
  <div class="home">

    <v-container fluid>
      <!-- title and search zone -->
      <v-layout>
        <h1>eBooks</h1>
        <v-spacer></v-spacer> 
        <v-text-field v-model="searchOne" label="Search" placeholder="Search a Title in eBooks..." append-outer-icon="mdi-magnify" outlined />
      </v-layout>
      <!-- title and search zone -->
      <!-- sortbtn zone -->
      <div class="mx-5">
           <v-btn small text color="grey" v-for="(f,i) in filterBtn" :key="i" @click="onClick(f.name)">
            <v-icon left small>{{ f.icon }}</v-icon>
            <span class="caption text-lowercase">{{ f.name }}</span>
           </v-btn>
           <v-btn small text color="grey" class="ml-10">
            <v-icon left small>mdi-archive-refresh</v-icon>
            <span class="caption text-lowercase" @click="reset">Reset</span>
           </v-btn>
      </div>
      <!-- sortbtn zone -->
      <!-- main content -->
      <v-row wrap>
        <v-col xs12 md6 v-for="(item, index) in pagedItems" :key="`item_index_${index}`" class="mx-5">
          <v-card class="mx-auto my-12" max-width="340" elevation="15">
            <v-img height="250" :src="item.src"></v-img>
            <v-card-title class="text-wrap">{{ item.title }}</v-card-title>
            <v-card-text>
              <span>{{ item.sub }}</span>
            </v-card-text>
            <v-card-actions>
              <v-chip small>{{ item.category }}</v-chip>
              <v-spacer />
              <v-btn outlined rounded :href="item.link" target="_blank" color="#005FFF">Start Reading</v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
        <v-col cols="12" md6>
          <v-pagination v-model="pageNo" :length="numPages" circle></v-pagination>
        </v-col>
      </v-row>
      <!-- main content -->

      <v-footer class="mt-6" padless>
        <v-col class="text-center font-weight-black" cols="12">{{ new Date().getFullYear() }} - IT Team</v-col>
      </v-footer>
    </v-container>

  </div>
</template>

<script>
import items from '../data/eBook' //I created a separate dummy data so it will not make this code long

  export default {
    data: () => ({
      filterBtn: [
        { name: 'Safety & Environment',
          icon: 'mdi-pine-tree'
        }, 
        { 
          name: 'Human Resources',
          icon: 'mdi-account-supervisor'
        }, 
        { 
          name: 'Port Security',
          icon: 'mdi-shield-lock'
        }, 
        { 
          name: 'Maritime Industry',
          icon: 'mdi-submarine'
        }, 
        { 
          name: 'Health Standards',
          icon: 'mdi-bottle-tonic-plus'
        },
        {
          name: 'Others',
          icon: 'mdi-ballot'
        }
        ],
      searchOne: '',
      filterer: '',
      items,
      pageSize: 12,
      pageNo: 1
    }),
    computed: {
      filteredBtn() {
        if (this.filterer === '') return this.items
        else return this.items.filter(f => f.category === this.filterer)
      },
      filteredItems() {
        if (this.searchOne) {
          return this.items.filter((item) => {
            return item.title.toLowerCase().includes(this.searchOne.toLowerCase())
        })
        } else {
          return this.filteredBtn
        }
      },
      numPages() {
        return Math.ceil(this.filteredItems.length / this.pageSize)
      },
      pagedItems() {
        const startIndex = (this.pageNo - 1) * this.pageSize
        const data = [...this.filteredItems]

        return data.splice(startIndex, this.pageSize)
      }
    },
    methods: {
      onClick(btn) {
        this.filterer = btn
        this.counter = this.items.length
        console.log(this.counter)
      },
      reset() {
        window.location.reload()
      }
    },
    mounted() {
      console.log(this.items.length)
    }
  }
</script>

<style>
.v-card__text, .v-card__title {
  word-break: normal; /* This is to noramlize the words in vuetify */
}
</style>
